import React, {  useEffect, useState } from 'react';
import { Route, Switch } from "react-router-dom";
import ErrorBoundary from 'antd/lib/alert/ErrorBoundary';
import { Loader } from 'platyplex_ui';
import { Home, Client, PermitsCalpoly, Map, Events } from './pages';
import logo from './assets/parkstash_logo.png'
import { useSelector } from 'react-redux';
import { Client as ClientType } from './store/reducer/clients';
import Help from './pages/help';
import Legal from './pages/legal';
import SubVisitor from './pages/sub-visitor';
import Visitor from './pages/visitor/calpoly';
import { Permit, RootPermit } from './store/reducer/permit';

export const toRoute = (str: string) => str.toLowerCase().replace(/\s+/g, "-");

function App() {
  const [ state, setState ] = useState({loading: true});
  const { clients } = useSelector((s: any) => s.clients);
  const { permits } = useSelector((s: any) => s.permits);
  useEffect(()=>{ 
      setTimeout(()=>setState({...state, loading: false}), 2000)
}, []);

  return (
   !state.loading? <ErrorBoundary>
        <Switch> 
          {clients.map((c: ClientType) => (
          <Route exact path={`/e-permits/:id`} render={() => <PermitsCalpoly client={c}/>} />))}

          {permits.map((p: RootPermit, i) => (
          <Route exact path={"/visitor/"+ i} render={() => <Visitor index={i} permit={p}/>} />))}


          {permits.map((p: RootPermit, i) => p.residential.map((per: Permit, j)=><Route exact path={`/visitor/${i}/sub-resident/${j}`} render={() => <Visitor index={i} permit={p} name={per.firstName}/>} /> ))}

          {permits.map((p: RootPermit, i) => p.visitor.map((per: Permit, j)=><Route exact path={`/visitor/${i}/sub-visitor/${j}`} render={() => <SubVisitor rootPermit={p} permit={per} name={per.firstName}/>} /> ))}

          <Route exact path="/citations/:id" render={() => <Events/>} />
          <Route exact path="/e-permits" render={() => <PermitsCalpoly/>} />
          <Route exact path="/client" render={() => <Client/>} />
          <Route exact path="/map" render={() => <Map/>} />
          <Route exact path="/legal" render={() => <Legal/>} />
          <Route exact path="/help" render={() => <Help/>} />
          <Route path="/" render={() => <Home/>} />
        </Switch>
    </ErrorBoundary>: <Loader.Custom logo={logo} />
  );
}

export default App;